@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

body {
    font-family: 'Lato', sans-serif;
    background-color: #efefef;
    padding-bottom: 5vh;

    background: -moz-linear-gradient(41deg, #6C8B94 0, #F6F6F6 100%);/* FF3.6+ */
    background: -webkit-gradient(linear, 41deg, color-stop(0, 6C8B94), color-stop(100%, F6F6F6));/* Chrome,Safari4+ */
    background: -webkit-linear-gradient(41deg, #6C8B94 0, #F6F6F6 100%);/* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(41deg, #6C8B94 0, #F6F6F6 100%);/* Opera 11.10+ */
    background: -ms-linear-gradient(41deg, #6C8B94 0, #F6F6F6 100%);/* IE10+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1301FE', endColorstr='#F4F60C', GradientType='1'); /* for IE */
    background: linear-gradient(41deg, #6C8B94 0, #F6F6F6 100%);/* W3C */
}

.small-text {
    font-size: 12px;
}